.skill {
    width: 400px; /* Хочу размеры больше, пофиксим мед-запросами*/
    /*background-color: #0e3c5a;*/
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 25px 5px 5px 5px;
}

.icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 250px;
    background-color: #3a3f62;
    background-size: cover;
    background-position: center;
}



.title {
    /*background-color: #b08585;*/
    color: white;
}

.description {
    /*background-color: #966262;*/
    font-size: 30px;
    font-weight: 600;
    color: white;

}

.projectButton {
    background-color: rgb(255, 255, 255,0.5);
    color: black;
    font-size: 30px;
    font-weight: 600;
    width: 250px;
    border-radius: 25px;
    border: none;
}

.projectButton:hover{
    background-color: rgb(0, 3, 16,0.5);
    width: 250px;
    border-radius: 25px;
    color: white;
    border: none;
}
